import React from 'react'
import {
  Card,
  CardTitle,
  CardText,
  CardSubtitle,
  CardBody,
} from 'reactstrap'
import { Link } from 'gatsby'
import { slugify } from '../util/utilityFunctions'

const LastPost = ({ title, author, slug, date, body, fluid, tags, link }) => (
  <Card className="col-sm-12 col-md-4"  >
   
   
    <CardBody>
      <CardTitle>
        <Link to={link}>{title}</Link>
      </CardTitle>
      <CardSubtitle>
        <span className="text-info">{date}</span>
          {/* by{' '}<span className="text-info">{author}</span>*/}
      </CardSubtitle>

      <CardText className="m4rem">{body}</CardText>
      <div className="list-tags">
        {tags.map( (tag,index, array) => (

            <Link to={`/tags/${slugify(tag)}`} key={tag} test='LASTPOST'>
                {tag} {index<array.length -1? ', ':''}
            </Link>

        ))}
      </div>
      <Link
        to={link}
        className="btn float-right text-uppercase"
      >
        Read more
      </Link>
    </CardBody>
   
  </Card>
);

export default LastPost
